"use strict";

import './style.less';

// 
const React = require('react');
const limit = require('limit');
const KeySearch = require('modules/keySearch/index');

// 
class Info extends React.Component {
	constructor(){
		super(...arguments);
		let me = this;
	}
	render(){
		let me = this;
		let props = me.props;
		let num = Actions(me).getMovieNumber();
		return (
			<div>
				<div className="thetop-info">
					<span className="ch-num">{num}</span>
				</div>
				<div className="thetop-info fn-MT10 thetop-search">
					<KeySearch width="320" placeholder="" />
				</div>
			</div>
		);
	}
};

module.exports = Info;




